let QARR = {};
QARR.Widgets = {};

QARR.Widgets.StatusStats = Garnish.Base.extend({
    settings: null,
    data: null,

    $widget: null,
    $body: null,

    init(widgetId, settings) {
        this.setSettings(settings);

        this.$widget = $('#widget' + widgetId);
        this.$body = this.$widget.find('.body:first');
    }
});

QARR.Widgets.PendingItemsWidget = Garnish.Base.extend({
    $container: null,
    $items: null,
    $currentItem: null,

    type: null,
    limit: null,
    dragger: null,
    exclude: null,

    init(el) {
        let that = this;
        this.exclude = [];
        this.$container = $(el);
        this.limit = this.$container.data('limit');
        this.type = this.$container.data('type');
        this.$items = this.$container.find('.list-item:not(.excluded)');

        if (this.$items.length > 0) {
            $.each(this.$items, function (i, item) {
                let id = $(item).data('id');
                that.exclude.push(id);
                new QARR.Widgets.PendingItem(item, that);
            });
        }
    },

    addPendingItem(item) {
        new QARR.Widgets.PendingItem(item, this);
    },

    fetchNewExcludeIds() {
        let that = this;
        this.exclude = [];

        $.each(this.$container.find('.list-item:not(.excluded)'), function (i, item) {
            let id = $(item).data('id');
            that.exclude.push(id);
        });

        return this.exclude;
    },

    fetchNewItem() {
        let count = 1;

        let payload = {
            type: this.type,
            limit: count,
            exclude: this.fetchNewExcludeIds()
        };

        Craft.postActionRequest('qarr/elements/fetch-pending-items', payload, $.proxy(((response, textStatus) => {
            if (response.success) {
                let itemHtml = $(response.template);
                if (response.count === 0) {
                    if (this.fetchNewExcludeIds().length === 0) {
                        let $empty = $(
                            '<div class="list-item excluded">' +
                            '<div class="item-wrapper">' +
                            '<p>'+ Craft.t("qarr", "No recent submissions") +'</p>' +
                            '</div>' +
                            '</div>'
                        );
                        this.$container.find('.list').html(itemHtml);
                        // this.$container.find('.list').html($empty);
                    }
                } else {
                    this.$container.find('.list').append(itemHtml);
                    itemHtml.addClass('new-item');
                    this.addPendingItem(this.$container.find(itemHtml));
                }
            }
        }), this));
    }
});


QARR.Widgets.PendingItem = Garnish.Base.extend({
    $item: null,
    $itemWrapper: null,
    $progress: null,

    $actionBtn: null,
    $approveItemBtn: null,
    $deleteItemBtn: null,
    $rejectItemBtn: null,

    elementId: null,
    type: null,
    dragger: null,
    margin: null,
    direction: null,

    parent: null,
    hud: null,

    dragStartMargin: null,

    init(el, parent) {
        this.parent = parent;
        this.$item = $(el);
        this.$progress = this.$item.find('.progress');
        this.$itemWrapper = this.$item.find('.item-wrapper');
        this.$actionBtn = this.$item.find('.action-btn');
        this.$approveItemBtn = this.$item.find('.approve-btn');
        this.$deleteItemBtn = this.$item.find('.delete-btn');
        this.$rejectItemBtn = this.$item.find('.reject-btn');
        this.elementId = this.$item.data('id');
        this.type = this.parent.type;

        this.dragger = new Garnish.BaseDrag(this.$item, {
            axis: Garnish.X_AXIS,
            onDragStart: $.proxy(this, '_onDragStart'),
            onDrag: $.proxy(this, '_onDrag'),
            onDragStop: $.proxy(this, '_onDragStop')
        });

        this.addListener(this.$actionBtn, 'click', 'performAction')
    },

    performAction(e) {
        let that = this;
        e.preventDefault();
        let actionType = $(e.currentTarget).data('action-type');

        if (actionType === 'delete') {
            $form = $(
                '<section class="hud-wrapper">' +
                    '<label for="">'+ Craft.t("qarr", "Are you sure?") +'</label>' +
                '</section>' +
                '<section class="hud-footer qarr-footer">' +
                    '<input type="button" class="btn-modal cancel" value="'+ Craft.t("qarr", "Cancel") +'">' +
                    '<input type="submit" class="btn-modal submit" value="'+ Craft.t("qarr", "Delete") +'"/>' +
                '</section>'
            ).appendTo(Garnish.$bod);

            this.hud = new Garnish.HUD(this.$deleteItemBtn, $form, {
                hudClass: 'hud qarr-hud',
                bodyClass: 'body',
                closeOtherHUDs: false
            });

            let $cancelBtn = this.hud.$footer.find('.cancel');

            this.addListener($cancelBtn, 'click', function() {
                this.hud.hide();
                this._putBack();
            });

            this.hud.on('submit', function(e) {
               that.deleteElement();
               that.hud.hide();
            });
        }

        if (actionType === 'reject') {
            Craft.postActionRequest('qarr/elements/update-status', this.buildPayload('rejected'), $.proxy(((response, textStatus) => {
                if (response.success) {
                    Craft.cp.displayNotice(Craft.t('qarr', 'Item Rejected'));
                    that.$rejectItemBtn.addClass('rejected');
                    that.$item.addClass('reject-item');
                    window.qarrnav._getPendingEntries();

                    setTimeout(function(){
                        that.parent.fetchNewItem();
                        that.$item.remove();
                    }, 500);
                }
            }), this));
        }
    },

    deleteElement() {
        let that = this;
        let payload = {
            id: this.elementId
        };

        Craft.postActionRequest('qarr/reviews/delete', payload, $.proxy(((response, textStatus) => {
            if (response.success) {
                Craft.cp.displayNotice(Craft.t('qarr', 'Item deleted'));
                that.$item.addClass('delete-item');
                window.qarrnav._getPendingEntries();

                setTimeout(function(){
                    that.parent.fetchNewItem();
                    that.$item.remove();
                }, 700);
            }
        }), this));
    },

    showPositiveMenu() {
        let that = this;

        Craft.postActionRequest('qarr/elements/update-status', this.buildPayload('approved'), $.proxy(((response, textStatus) => {
            if (response.success) {
                Craft.cp.displayNotice(Craft.t('qarr', 'Item Approved'));
                that.$approveItemBtn.addClass('approved');
                that.$item.addClass('approve-item');

                setTimeout(function(){
                    window.qarrnav._getPendingEntries();
                    that.parent.fetchNewItem();
                    that.$item.remove();
                }, 500);
            }
        }), this));
    },

    buildPayload(status) {
        let payload = {
            id: this.elementId,
            status: status,
            type: this.type
        };

        return payload
    },

    showNegativeMenu() {
        this.$item.addClass('other-menu');
    },

    _onSettle() {
        this.$item.removeClass('dragging');
        this.$item.removeClass('other-menu');
    },

    _onDragStart() {
        this.$item.addClass('dragging');
        this.dragStartMargin = this._getPosition();
    },

    _onDrag() {
        this.$item.removeClass('other-menu');
        let position;

        if (Craft.orientation === 'ltr') {
            position = this.dragStartMargin + this.dragger.mouseDistX;
        } else {
            position = this.dragStartMargin - this.dragger.mouseDistX;
        }

        if (position < this._getLeftPull()) {
            position = this._getLeftPull();
        } else if (position > 0) {
            if (position < this._getRightPull()) {
                position = this.dragStartMargin + this.dragger.mouseDistX;
            } else {
                position = this._getRightPull();
            }
        }

        this.$itemWrapper.css(Craft.left, position);
    },

    _onDragStop() {
        let position = this._getPosition();

        if (position === (this._getLeftPull())) {
            this.showNegativeMenu();
        } else {

            if (position !== (this._getRightPull())) {
                this._putBack();
            }
        }

        if (position === (this._getRightPull())) {
            this.showPositiveMenu()
            this.$itemWrapper.css(Craft.left, this._getRightPull());
        }
    },

    _putBack() {
        this.$item.removeClass('dragging');
        this.$item.removeClass('other-menu');
        let animateCss = {};
        animateCss[Craft.left] = 0;
        this.$itemWrapper.velocity('stop').velocity(animateCss, QARR.Widgets.PendingItem.animationDuration);
    },

    _getLeftPull() {
        return -200;
    },

    _getRightPull() {
        return 100;
    },

    _getPosition: function() {
        return parseInt(this.$itemWrapper.css(Craft.left));
    },
}, {
    animationDuration: 100,
    defaults: {
        value: '1',
        onChange: $.noop
    }
});

Garnish.$doc.ready(() => {
    if ($('#widget-recent-reviews').length > 0) {
        let pendingReviews = new QARR.Widgets.PendingItemsWidget('#widget-recent-reviews');
    }

    if ($('#widget-recent-questions').length > 0) {
        let pendingQuestions = new QARR.Widgets.PendingItemsWidget('#widget-recent-questions');
    }
});


